.header {
  flex-shrink: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  transition: background-color $trs2;
  &_fixed {
    background-color: var(--color-bg-transparent);
    .header__container {
      height: 70px;
    }
  }
  &_nav-opened {
    background-color: transparent;
    .header__container {
      height: 130px;
    }
  }
  &__container {
    height: 130px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: height $trs2;
  }
  @include media-breakpoint-down(xxl) {
    &__container {
      height: 100px;
    }
    &_nav-opened {
      .header__container {
        height: 100px;
      }
    }
  } 
}

.nav-toggle {
  width: 50px;
  height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 14px 8px;
  margin-right: 12px;
  svg {
    width: 100%;
    fill: none;
    color: var(--color-light);
    path {
      stroke-width: 2.5px;
      transition: $trs1;
    }
  }
}
