h1,h2,h3,h4,h5,h6 {
  margin: 2.75rem 0 2.25rem 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  line-height: 1.4;
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
  & + h2,
  & + h3,
  & + h4,
  & + h5,
  & + h6 {
    margin-top: 2.25rem;
  }
}
h1, .h1 {
  font-size: 4rem;
  line-height: 1.2;
}
h2, .h2 {
  font-size: 2.5rem;
}
h3, .h3 {
  font-size: 2rem;
}
h4, .h4 {
  font-size: 1.75rem;
}
h5, .h5 {
  font-size: 1.5rem;
}
h6, .h6 {
  font-size: 1.25rem;
}

p {
  margin: 0 0 1.75rem 0;
  &:last-child {
    margin: 0;
  }
}
mark {
  background-color: transparent;
  color: var(--color-accent);
}

img:not([class]) {
  display: block;
  width: auto;
  max-width: 100%;
}
strong, b {
  font-weight: 700;
}

ul:not([class]), ol:not([class]) {
  &:not(:last-child) {
    margin-bottom: 1.25rem;
  }
  li:not([class]) {
    &:not(:last-child) {
      margin: 0 0 0.75rem 0;
    }
  }
}

ul:not([class]) {
  li:not([class]) {
    position: relative;
    padding-left: 30px;
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 9px;
      width: 10px;
      height: 10px;
      background-color: var(--color-accent);
      transform: rotate(45deg);
    }
  }
}

ol:not([class]) {
  counter-reset: section;
  li:not([class]) {
    &:before {
      counter-increment: section;
      content: counter(section) '. ';
    }
  }
}

a {
  color: inherit;
  text-decoration: none;
  transition: color $trs1;
  span:not([class]) {
    transition: border-color $trs1;
    border-bottom: 1px solid transparent;
  }
  &.touch, &.hover {
    color: var(--color-light);
    span:not([class]) {
      border-color: var(--color-light);
    }
  }
  &.active {
    pointer-events: none;
  }
  &.focus {
    color: var(--color-accent);
    span:not([class]) {
      border-color: transparent;
    }
  }
}

@include media-breakpoint-down(xxl) {
  h1, .h1 {
    font-size: 3.625rem;
  }
  h2, .h2 {
    font-size: 2.375rem;
  }
}